import { IArea } from '../business/area';

export interface IUserFavorites {
  leagues: IUserFavoriteLeague[];
  teams: IUserFavoriteTeam[];
  players: IUserFavoritePlayer[];
  favoriteCount: number;
}

export interface IUserFavoriteLeague {
  id: number;
  name: string;
  gender: string;
  divisionLevel: number;
  area: IArea;
}

export interface IUserFavoriteTeam {
  id: number;
  name: string;
  officialName: string;
  imageDataUrl: string;
  leagueId: number;
  area: IArea;
}

export interface IUserFavoritePlayer {
  id: number;
  passportArea: IArea;
  birthArea: IArea;
  roleId: number;
  birthDate: string;
  lastName: string;
  firstName: string;
  imageDataURL: string;
  team: IUserFavoriteTeam;
  totalScore: number;
}
