import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Pipe({
  name: 'customTranslate',
})
export class CustomTranslatePipe implements PipeTransform {
  constructor(private _translateService: TranslateService) {}
  transform(key: string, language: string): Observable<string> {
    const keys = key.split('.');
    return this._translateService.getTranslation(language).pipe(
      map(translation => {
        if (Object.keys(translation).length) {
          let auxTranslation = translation;
          keys.forEach(elem => {
            auxTranslation = auxTranslation[elem];
          });

          return auxTranslation;
        }
        return key;
      })
    );
  }
}
