import { IArea } from './area';
import { IPlayerRole } from './player-role';
import { ITeam } from './team';
import { IPlayerHighlight, IPlayerPosition } from './player-highlights';

export interface IPlayer {
  id: number;
  playerId?: number;
  wyscoutId: number;
  shortName?: string;
  firstName: string;
  middleName?: string;
  lastName: string;
  fullName?: string;
  height?: number;
  weight?: number;
  birthDate: string;
  formattedBirthDate?: string;
  birthAreaId?: number;
  birthArea?: IArea;
  passportAreaId?: number;
  passportArea?: IArea;
  roleId: number;
  role: IPlayerRole;
  foot: string;
  currentTeamId: number;
  currentTeam?: ITeam;
  currentNationalTeamId?: number;
  gender?: string;
  status?: string;
  gsmId?: number;
  imageDataURL?: string;
  career: Array<IPlayerCareer>;
  transfers: Array<IPlayerTransfer>;
  algorithmScore?: number;
  analystScore?: number;
  totalScore?: number;
  creationDate?: string;
  modificationDate?: string;
  observations?: string;
  playerPositions?: IPlayerPosition[];
  highlights: IPlayerHighlight;
}

export interface IPlayerCareer {
  appearances: number;
  areaName: string;
  goal: number;
  leagueId: number;
  leagueName: string;
  minutesPlayed: number;
  penalties: number;
  redCards: number;
  seasonEnd: Date;
  seasonId: number;
  seasonName: string;
  seasonStart: string;
  shirtNumber: number;
  substituteIn: number;
  substituteOnBench: number;
  substituteOut: number;
  teamId: number;
  teamName: string;
  yellowCard: number;
}

// eslint-disable-next-line @typescript-eslint/naming-convention
export const PLAYER_TRANSFER_TYPES = {
  TRANSFER: 'Transfer',
  BACK_FROM_LOAN: 'Back from Loan',
  LOAN: 'Loan',
  FREE_TRANSFER: 'Free Transfer',
};

export type PLAYER_TRANSFER_TYPE = 'Transfer' | 'Back from Loan' | 'Loan' | 'Free Transfer';

export interface IPlayerTransfer {
  id: number;
  active: boolean;
  endDate?: Date;
  fromTeamName: string;
  startDate: Date;
  toTeamName: string;
  type: PLAYER_TRANSFER_TYPE;
  year: number;
}

export interface IPlayerUpdateRequest {
  firstName: string;
  lastName: string;
  height: number;
  weight: number;
  foot: string;
  birthDate: string;
  birthAreaId: number;
  passportAreaId: number;
  observations: string;
}
