import { Component, Input } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { IPlayer, IPlayerStats, PlayerService } from '@aft-core';
import { Router } from '@angular/router';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-player-stat-summary',
  templateUrl: './player-stat-summary.component.html',
  styleUrls: ['./player-stat-summary.component.scss'],
})
export class PlayerStatSummaryComponent {
  playerDetails$: BehaviorSubject<IPlayer | undefined> | undefined;
  playerStats$: BehaviorSubject<IPlayerStats | undefined> | undefined;

  @Input()
  isAlgorithmStat: boolean = false;

  @Input()
  showPlayerResume: boolean = true;

  constructor(private _playerService: PlayerService, private _router: Router) {
    this.playerDetails$ = this._playerService.playerDetails$;
    this.playerStats$ = this._playerService.playerStats$;
  }

  goToTeam(player: IPlayer): void {
    this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, player.currentTeam?.id));
  }
}
