// eslint-disable-next-line @typescript-eslint/naming-convention
import { APP_ROUTES } from '@aft-core';

// eslint-disable-next-line @typescript-eslint/naming-convention
export const FIXTURE_MODULE_ROUTES = {
  MODULE_ROUTE: 'fixture',
  FIXTURE: '',
  FIXTURE_FILTERED_ROUTE: 'list/:leagueId/:dateId',
  FIXTURE_SEMI_FILTERED_ROUTE: 'list/:leagueId',
  FIXTURE_FILTERED: 'list',
  MATCH_ROUTE: 'match/:matchId',
  MATCH_DETAILS: 'match',
  TEAM_ROUTE: 'team/:teamId',
  TEAM_DETAILS: 'team',
};

export const generateFixtureModuleRoute = (route: string): Array<string> => {
  if (route?.length) {
    return [route];
  }
  return [];
};

export const generateFullFixtureModuleRoute = (route: string, param1?: any, param2?: any): Array<string> => {
  const routeArray = [...APP_ROUTES.PRIVATE, FIXTURE_MODULE_ROUTES.MODULE_ROUTE, ...generateFixtureModuleRoute(route)];
  if (param1) {
    routeArray.push(param1);
  }
  if (param2) {
    routeArray.push(param2);
  }
  return routeArray;
};
