import { Component, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { NavigationEnd, Router } from '@angular/router';
import { environment } from '../environments/environment';

import { AuthService, LANGUAGES_ENUM, STORAGE_KEYS_ENUM } from '@aft-core';

declare let gtag: (property: string, value: any, configs: any) => {};

@Component({
  selector: 'aft-scout-root',
  templateUrl: './init.component.html',
})
export class InitComponent implements OnInit, OnDestroy {
  title = 'scouting-portal';
  private _subscriptions$: Subscription[] = [];

  constructor(private _translate: TranslateService, private _authService: AuthService, private _router: Router) {
    const lang = window.localStorage.getItem(STORAGE_KEYS_ENUM.LANGUAGE) || navigator.language;
    // lenguaje por defecto si la traducción no es encontrada
    _translate.setDefaultLang(LANGUAGES_ENUM.ES);

    // el lenguaje a usar, si no esta disponible, usara el por defecto
    _translate.use(lang);
  }

  ngOnInit(): void {
    this._gtagNavigationHandler();
    this._authService.setLoginStatus();
  }

  private _gtagNavigationHandler(): void {
    // Indicador de loading entre pantallas
    this._subscriptions$.push(
      this._router.events.subscribe(val => {
        // Limpiar estado de navegación al NavigationEnd
        if (val instanceof NavigationEnd) {
          gtag('config', environment.googleAnalyticsId, {
            page_path: val.urlAfterRedirects,
          });
        }
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions$.forEach(sub => sub.unsubscribe());
  }
}
