export interface IMatch {
  id: number;
  leagueId: number;
  leagueName: string;
  roundId: number;
  roundName: string;
  seasonId?: number;
  seasonName?: string;
  status: TMatchStatus;
  dateutc: Date;
  gameweek: number;
  winnerTeamId: number | undefined | null;
  homeTeam: ITeamMatch;
  awayTeam: ITeamMatch;
}
export interface ITeamMatch {
  id: number;
  name: string;
  officialName: string;
  imageDataURL: string;
  score?: number;
}

export type TMatchStatus = 'Played' | 'Playing' | 'Fixture' | 'Suspended' | 'Awarded' | 'Postponed' | 'Cancelled';
