import { Component, Input, OnInit } from '@angular/core';
import { IPlayerRole, PLAYER_ROLES_ARRAY } from '@aft-core';

@Component({
  selector: 'aft-player-position',
  templateUrl: './player-position.component.html',
  styleUrls: ['./player-position.component.scss'],
})
export class PlayerPositionComponent implements OnInit {
  @Input()
  position: string | undefined;

  @Input()
  roleId: number | undefined;

  private _role: IPlayerRole | undefined;

  ngOnInit(): void {
    if (this.roleId) {
      this._role = PLAYER_ROLES_ARRAY.find(r => r.id === this.roleId);
    }
  }

  get role(): IPlayerRole | undefined {
    return this._role;
  }
}
