// eslint-disable-next-line @typescript-eslint/naming-convention
import { APP_ROUTES } from '@aft-core';

// eslint-disable-next-line @typescript-eslint/naming-convention
export const PRIVATE_MODULE_ROUTES = {
  HOME: '',
  PROFILE: 'profile',
  ADVANCED_SEARCH: 'adv-search',
  PLAYER: 'player',
  LEAGUE_ROUTE: 'league/:leagueId',
  LEAGUE: 'league',
  FIXTURE: 'fixture',
  STANDINGS: 'standings/:leagueId',
  FAVORITES: 'favorites',
};

export const generatePrivateModuleRoute = (route: string): Array<string> => {
  if (route?.length) {
    return [route];
  }
  return [];
};

export const generateFullPrivateModuleRoute = (route: string, params?: any): Array<string> => {
  const routeArray = [...APP_ROUTES.PRIVATE, ...generatePrivateModuleRoute(route)];
  if (params) {
    routeArray.push(params);
  }
  return routeArray;
};
