import { APP_INITIALIZER, ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { GoogleLoginProvider, SocialAuthServiceConfig, SocialLoginModule } from 'angularx-social-login';

import {
  AppConfigService,
  ApiService,
  AuthService,
  TokenService,
  LoadingService,
  UserService,
  PaymentService,
  TeamsPlayersService,
  PlayerService,
  LeaguesService,
  FixtureService,
  EventsService,
  MediaService,
} from './services';
import { AppFileConfig, IAppFileConfig } from './interfaces';
import { TokenInterceptor, HttpLoadingRequestInterceptor, SessionInterceptor } from './interceptors';
import {
  AlgorithmGuard,
  ImportsGuard,
  InvitesGuard,
  PaymentsGuard,
  MassiveEditGuard,
  PrivateCanActivateChildGuard,
  PrivateGuard,
  PublicGuard,
  UsersGuard,
} from './guards';

// Initialize Config service

export function initializeConfigService(appConfigService: AppConfigService) {
  return (): Promise<any> => {
    return appConfigService.loadAppConfig();
  };
}

const googleLoginOptions = {
  scope: 'profile email',
};

@NgModule({
  declarations: [],
  imports: [HttpClientModule, SocialLoginModule],
  exports: [HttpClientModule, CommonModule, SocialLoginModule],
  providers: [
    AuthService,
    TokenService,
    UserService,
    PaymentService,
    TeamsPlayersService,
    ApiService,
    LoadingService,
    PlayerService,
    PublicGuard,
    PrivateGuard,
    AlgorithmGuard,
    ImportsGuard,
    InvitesGuard,
    PaymentsGuard,
    MassiveEditGuard,
    UsersGuard,
    PrivateCanActivateChildGuard,
    LeaguesService,
    FixtureService,
    EventsService,
    MediaService,
    { provide: HTTP_INTERCEPTORS, useClass: TokenInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: HttpLoadingRequestInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: SessionInterceptor, multi: true },
  ],
})
export class AftCoreModule {
  // Prevents initialization of google api providers if not present
  static buildProviders(appConfig: IAppFileConfig, googleApiKeyId: string): Array<any> {
    const providers: Array<any> = [
      AppConfigService,
      {
        provide: AppFileConfig,
        useValue: appConfig,
      },
      {
        provide: APP_INITIALIZER,
        useFactory: initializeConfigService,
        deps: [AppConfigService],
        multi: true,
      },
    ];

    const socialAuthConfigProviders: any = [];
    if (googleApiKeyId.length) {
      socialAuthConfigProviders.push({
        // use env file to obtain google api key
        id: GoogleLoginProvider.PROVIDER_ID,
        provider: new GoogleLoginProvider(googleApiKeyId, googleLoginOptions),
      });
    }

    // Initializes the social auth module
    providers.push({
      provide: 'SocialAuthServiceConfig',
      useValue: {
        autoLogin: false,
        providers: socialAuthConfigProviders,
      } as SocialAuthServiceConfig,
    });
    return providers;
  }

  static forRoot(appConfig: IAppFileConfig, googleApiKeyId: string = ''): ModuleWithProviders<any> {
    return {
      ngModule: AftCoreModule,
      providers: this.buildProviders(appConfig, googleApiKeyId),
    };
  }
}
