import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import {
  ApiService,
  IDefaultResponse,
  ILeague,
  MessagingService,
  ENTITIES_ENUM,
  ITeam,
  IPlayer,
  API_TYPE_ENUM,
  ISearchResult,
} from '@aft-core';

@Injectable()
export class SearchService {
  isLoading$ = new BehaviorSubject<boolean>(false);
  searchResults$ = new BehaviorSubject<Array<ISearchResult>>([]);

  constructor(private _apiService: ApiService, private _messagingService: MessagingService) {}

  private _handleSearchResult = (res: IDefaultResponse): void => {
    const searchResults: ISearchResult[] = [];
    res?.element?.leagues?.forEach((league: ILeague) => {
      searchResults.push({ id: league.id, name: league.name, entity: ENTITIES_ENUM.LEAGUE } as ISearchResult);
    });
    res?.element?.teams?.forEach((team: ITeam) => {
      searchResults.push({ id: team.id, name: team.name, entity: ENTITIES_ENUM.TEAM } as ISearchResult);
    });
    res?.element?.players?.forEach((player: IPlayer) => {
      searchResults.push({ id: player.id, name: player.fullName, entity: ENTITIES_ENUM.PLAYER } as ISearchResult);
    });

    this.isLoading$.next(false);
    this.searchResults$.next(searchResults);
  };

  searchData(toSearch: string): void {
    this.isLoading$.next(true);
    const url = `extras/${toSearch}`;
    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(this._handleSearchResult, () => {
      this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
      this.isLoading$.next(false);
    });
  }
}
