import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError, map, take } from 'rxjs/operators';
import { IDefaultResponse, IEntityStatus } from '../../interfaces';
import { API_TYPE_ENUM } from '../../enums';
import { ApiService } from '../system/api.service';

@Injectable({
  providedIn: 'root',
})
export class SyncStatusService {
  isLoading$ = new BehaviorSubject<boolean>(false);

  constructor(private _apiService: ApiService) {}

  getEntitySyncStatus(entity: string, entityId?: number): Observable<IEntityStatus> {
    this.isLoading$.next(true);
    let url = `sync/entity/${entity.toLowerCase()}/status`;
    if (entityId) {
      url += `?entityId=${entityId}`;
    }
    return this._apiService.get(API_TYPE_ENUM.LEAGUES, url).pipe(
      map(
        (res: IDefaultResponse) => {
          this.isLoading$.next(false);
          return res?.element;
        },
        catchError(err => {
          this.isLoading$.next(false);
          throw new Error(err);
        })
      )
    );
  }

  triggerEntitySyncStatus(entity: string, entityId?: any): Observable<boolean> {
    this.isLoading$.next(true);
    let url = `sync/entity/${entity.toLowerCase()}`;
    if (entityId) {
      url = `${url}/${entityId}`;
    }
    return this._apiService.get(API_TYPE_ENUM.LEAGUES, url).pipe(
      take(1),
      map(() => {
        this.isLoading$.next(false);
        return true;
      }),
      catchError(() => {
        this.isLoading$.next(false);
        return of(false);
      })
    );
  }
}
