import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { debounceTime, skip } from 'rxjs/operators';
import { BehaviorSubject, Subscription } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { ENTITIES_ENUM, ISearchResult } from '@aft-core';
import { SearchService } from '../../services/search.service';

@Component({
  selector: 'aft-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.scss'],
  providers: [SearchService],
})
export class SearchBarComponent implements OnInit, OnDestroy {
  @Input()
  playerRedirectionRoute: Array<string | number> | undefined;

  @Input()
  teamRedirectionRoute: Array<string | number> | undefined;

  @Input()
  leagueRedirectionRoute: Array<string | number> | undefined;

  @Input()
  advSearchRedirectionRoute: Array<string | number> | undefined;

  form: FormGroup;
  hasResults: boolean = false;
  searchFired: boolean = false;
  searchResults: ISearchResult[] = [];
  loading$: BehaviorSubject<boolean>;

  private _subscriptions: Subscription[] = [];

  constructor(
    private _fb: FormBuilder,
    private _searchService: SearchService,
    private _translateService: TranslateService,
    private _router: Router
  ) {
    this.form = this._fb.group({
      search: [''],
    });
    this._subscriptions.push(
      this.searchCtrl.valueChanges.pipe(debounceTime(500)).subscribe((searchString: string) => {
        if (searchString?.length > 1) {
          this.searchFired = true;
          this._searchService.searchData(searchString);
        } else {
          this.searchResults = [];
          this.searchFired = false;
        }
      })
    );

    this.loading$ = this._searchService.isLoading$;
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this._searchService.searchResults$.pipe(skip(1)).subscribe(searchResults => {
        if (searchResults.length) {
          this.hasResults = true;
          this.searchResults = searchResults;
        } else {
          this.hasResults = false;
          this.searchResults = [];
        }
      })
    );
  }

  getEntityLabel(entity: ENTITIES_ENUM): string {
    switch (entity) {
      case ENTITIES_ENUM.LEAGUE:
        return this._translateService.instant('GENERAL.ENTITIES.LEAGUE');
      case ENTITIES_ENUM.TEAM:
        return this._translateService.instant('GENERAL.ENTITIES.TEAM');
      case ENTITIES_ENUM.PLAYER:
        return this._translateService.instant('GENERAL.ENTITIES.PLAYER');
      default:
        return this._translateService.instant('GENERAL.ENTITIES.LEAGUE');
    }
  }

  goToEntity(entity: ISearchResult): void {
    if (entity.entity === ENTITIES_ENUM.LEAGUE && this.leagueRedirectionRoute?.length) {
      this._router.navigate([...this.leagueRedirectionRoute, entity.id]);
    } else if (entity.entity === ENTITIES_ENUM.TEAM && this.teamRedirectionRoute?.length) {
      this._router.navigate([...this.teamRedirectionRoute, entity.id]);
    } else if (entity.entity === ENTITIES_ENUM.PLAYER && this.playerRedirectionRoute?.length) {
      this._router.navigate([...this.playerRedirectionRoute, entity.id]);
    } else {
      throw new Error('Redirection Route not recieved by input');
    }
    this.searchFired = false;
  }

  goToAdvancedSearch(): void {
    if (this.advSearchRedirectionRoute?.length) {
      this._router.navigate(this.advSearchRedirectionRoute);
    } else {
      throw new Error('Redirection Route not recieved by input');
    }
  }

  clearSearch(): void {
    this.searchCtrl.setValue('');
  }

  get searchCtrl(): AbstractControl {
    return this.form.controls.search;
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }
}
