import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { SocialAuthService } from 'angularx-social-login';

import { ApiService } from '../system/api.service';
import { API_TYPE_ENUM } from '../../enums';
import { TokenService } from '../system/token.service';
import { MessagingService } from '../ui/messaging.service';
import { IForgotRequest, ILoginRequest, ILoginResponse, IResetRequest } from '../../interfaces';
import { APP_ROUTES } from '../../app.routes';

@Injectable()
export class AuthService {
  isLoggedIn$ = new BehaviorSubject<boolean>(false);
  hasChangedPassword$ = new BehaviorSubject<boolean>(false);
  isPlatformLoggedIn$ = new BehaviorSubject<boolean>(false);

  constructor(
    private _router: Router,
    private _apiService: ApiService,
    private _messagingService: MessagingService,
    private _tokenService: TokenService,
    private _socialAuthService: SocialAuthService
  ) {
    this._socialAuthService.authState.subscribe(() => {
      this.isPlatformLoggedIn$.next(true);
    });
  }

  loginBackoffice(loginRequest: ILoginRequest): void {
    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'sessions/backoffice', loginRequest).subscribe(
      (response: ILoginResponse) => this.loginUser(response),
      () => this._handleLoginError()
    );
  }

  loginScouting(loginRequest: ILoginRequest): void {
    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'sessions/scouting', loginRequest).subscribe(
      (response: ILoginResponse) => this.loginUser(response),
      () => this._handleLoginError()
    );
  }

  setLoginStatus(): void {
    const hasValidToken = this._tokenService.isTokenValid();
    if (hasValidToken) {
      this.isLoggedIn$.next(true);
    }
  }

  logout(): void {
    this._tokenService.deleteToken();
    this.isLoggedIn$.next(false);
    this.isPlatformLoggedIn$.next(false);
    this._router.navigate(APP_ROUTES.LANDING);
  }

  sendForgotEmail(forgotRequest: IForgotRequest): void {
    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'users/ForgotPassword', forgotRequest).subscribe(() => {
      this._messagingService.success('FORGOT.TOAST.SUCCESS.TITLE', 'FORGOT.TOAST.SUCCESS.MESSAGE');
      this._router.navigate(APP_ROUTES.LANDING);
    });
  }

  resetPassword(resetRequest: IResetRequest): void {
    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'users/ResetPassword', resetRequest).subscribe(
      () => {
        this._messagingService.success('RESET.TOAST.SUCCESS.TITLE', 'RESET.TOAST.SUCCESS.MESSAGE');
        this.hasChangedPassword$.next(true);
      },
      () => {
        this._messagingService.error('RESET.TOAST.ERROR.TITLE', 'RESET.TOAST.ERROR.MESSAGE');
        this._router.navigate(APP_ROUTES.LANDING);
      }
    );
    this._router.navigate(APP_ROUTES.LANDING);
  }

  loginUser(response: ILoginResponse): void {
    this.isLoggedIn$.next(true);
    this.hasChangedPassword$.next(false);
    this._tokenService.saveToken(response.element.accessToken);
    this._router.navigate(APP_ROUTES.PRIVATE);
  }

  private _handleLoginError(): void {
    this.isLoggedIn$.next(false);
    this._messagingService.error('LOGIN.TOAST.ERROR.TITLE', 'LOGIN.TOAST.ERROR.MESSAGE');
    this._router.navigate(APP_ROUTES.LANDING);
  }
}
