import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';

import { ApiUtils } from '@aft-core';
import { IDefaultResponse, IEventType, IEvent, IPagination } from '../../interfaces';
import { ApiService } from '../system/api.service';
import { MessagingService } from '../ui/messaging.service';
import { API_TYPE_ENUM, EVENT_TYPE_ENUM } from '../../enums';

@Injectable({
  providedIn: 'root',
})
export class EventsService {
  events$ = new BehaviorSubject<Array<IEvent>>([]);
  pagination$ = new BehaviorSubject<IPagination>(ApiUtils.createPaginationObject());

  constructor(private _apiService: ApiService, private _messagingService: MessagingService) {}

  loadLeagueEvents(leagueId: number): void {
    const url = `events/league/${leagueId}`;
    this._getEvents(url);
  }

  loadTeamEvents(teamId: number): void {
    const url = `events/team/${teamId}`;
    this._getEvents(url);
  }

  loadMatchEvents(matchId: number): void {
    const url = `events/match/${matchId}`;
    this._getEvents(url);
  }

  loadMatchEventsAdvance(matchId: number): void {
    const url = `events/matches/${matchId}/advancedstats`;
    this._getEventsTest(url);
  }

  loadPlayerEvents(playerId: number, page: number = 0): void {
    const url = `events/player/${playerId}?page=${page}`;

    this._apiService
      .get(API_TYPE_ENUM.STATS, url)
      .pipe(map((res: IDefaultResponse) => res.element))
      .subscribe(
        (res: any) => {
          res.pageSize = 6;
          this.pagination$.next(ApiUtils.createPaginationObject(res));
          res.elements.map((event: IEvent) => {
            return EventsService._formatEvent(event);
          });
          this.events$.next(res.elements || []);
        },
        () => {
          this.events$.next([]);
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      );
  }

  private _getEvents(url: string): void {
    this._apiService
      .get(API_TYPE_ENUM.STATS, url)
      .pipe(map((res: IDefaultResponse) => res.element))
      .subscribe(
        (res: IEvent[]) => {
          console.log(res)
          res.map(event => {
            return EventsService._formatEvent(event);
          });
          this.events$.next(res || []);
        },
        () => {
          this.events$.next([]);
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      );
  }

  private _getEventsTest(url: string): void {
    this._apiService
      .get(API_TYPE_ENUM.STATS, url)
      .pipe(map((res: IDefaultResponse) => res.element))
      .subscribe(
        (res: IEvent[]) => {
          res.map(event => {
            return EventsService._formatEvent(event);
          });
          this.events$.next(res || []);
        },
        () => {
          this.events$.next([]);
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      );
  }

  private static _formatEvent(event: IEvent): IEvent {
    const { typeShortLabel, typeLabel } = this._getEventTypeLabel(event.type);
    event.typeShortLabel = typeShortLabel;
    event.typeLabel = typeLabel;
    const { teamScore, opponentTeamScore } = this._getScoreFromString(event.label);
    event.teamScore = teamScore;
    event.opponentTeamScore = opponentTeamScore;
    return event;
  }

  private static _getEventTypeLabel(eventType: IEventType): any {
    switch (true) {
      case eventType.secondary.includes(EVENT_TYPE_ENUM.PENALTY_GOAL):
        return { typeShortLabel: EVENT_TYPE_ENUM.GOAL, typeLabel: EVENT_TYPE_ENUM.PENALTY_GOAL };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.GOAL):
        return { typeShortLabel: EVENT_TYPE_ENUM.GOAL, typeLabel: EVENT_TYPE_ENUM.GOAL };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.SAVE):
      case eventType.secondary.includes(EVENT_TYPE_ENUM.SAVE_WITH_REFLEX):
        return { typeShortLabel: EVENT_TYPE_ENUM.SHOT_AGAINST, typeLabel: EVENT_TYPE_ENUM.SAVE };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.RED_CARD):
        return { typeShortLabel: EVENT_TYPE_ENUM.CARD, typeLabel: EVENT_TYPE_ENUM.RED_CARD };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.YELLOW_CARD):
        return { typeShortLabel: EVENT_TYPE_ENUM.CARD, typeLabel: EVENT_TYPE_ENUM.YELLOW_CARD };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.CONCEDED_GOAL):
        return {
          typeShortLabel: EVENT_TYPE_ENUM.SHOT_AGAINST,
          typeLabel: EVENT_TYPE_ENUM.CONCEDED_GOAL,
        };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.DEFENSIVE_DUEL):
        return {
          typeShortLabel: EVENT_TYPE_ENUM.DEFENSIVE_DUEL,
          typeLabel: EVENT_TYPE_ENUM.DEFENSIVE_DUEL,
        };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.PENALTY_CONCEDED_GOAL):
        return {
          typeShortLabel: EVENT_TYPE_ENUM.PENALTY_FOUL,
          typeLabel: EVENT_TYPE_ENUM.PENALTY_CONCEDED_GOAL,
        };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.PENALTY_FOUL):
        return {
          typeShortLabel: EVENT_TYPE_ENUM.PENALTY_FOUL,
          typeLabel: EVENT_TYPE_ENUM.PENALTY_FOUL,
        };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.PENALTY_SAVE):
        return {
          typeShortLabel: EVENT_TYPE_ENUM.PENALTY_FOUL,
          typeLabel: EVENT_TYPE_ENUM.PENALTY_SAVE,
        };
      case eventType.secondary.includes(EVENT_TYPE_ENUM.ASSIST):
        return { typeShortLabel: EVENT_TYPE_ENUM.PASS, typeLabel: EVENT_TYPE_ENUM.ASSIST };
      default:
        return EVENT_TYPE_ENUM.UNKNOWN;
    }
  }

  private static _getScoreFromString(label: string): any {
    const score = label?.match(/\d+/g);
    if (score) {
      const homeScore = score[0];
      const awayScore = score[1];
      return { teamScore: homeScore, opponentTeamScore: awayScore };
    } else {
      return { teamScore: null, opponentTeamScore: null };
    }
  }
}
