import { Component, Input } from '@angular/core';
import { UserService } from '@aft-core';

@Component({
  selector: 'aft-favorite-tag',
  templateUrl: './favorite-tag.component.html',
  styleUrls: ['./favorite-tag.component.scss'],
})
export class FavoriteTagComponent {
  favIcon: string = 'pi pi-bookmark';

  @Input() set entity(value: string) {
    this._entity = value;
    this._isFavorite();
  }

  @Input() set id(value: number) {
    this._id = value;
    this._isFavorite();
  }

  private _entity: string | undefined;
  private _id: number | undefined;

  isFav: boolean = false;

  constructor(private _userService: UserService) {}

  changeFavStatus(): void {
    if (this._id && this._entity) {
      if (this._userService.isFavorite(this._id, this._entity)) {
        this._userService.deleteFavorite(this._id, this._entity);
        this.favIcon = 'pi pi-bookmark';
      } else {
        this._userService.addFavorite(this._id, this._entity);
        this.favIcon = 'pi pi-bookmark-fill';
      }
    }
  }

  private _isFavorite(): void {
    if (this._id && this._entity) {
      this.isFav = this._userService.isFavorite(this._id, this._entity);
      this.favIcon = this.isFav ? 'pi pi-bookmark-fill' : 'pi pi-bookmark';
    }
  }

  get entity(): string {
    return this._entity || '';
  }

  get id(): number {
    return this._id || 0;
  }
}
