import { Injectable } from '@angular/core';
import { JwtHelperService } from '@auth0/angular-jwt';

import { STORAGE_KEYS_ENUM } from '../../enums';

@Injectable()
export class TokenService {
  private _helper;

  constructor() {
    this._helper = new JwtHelperService();
  }

  getToken(): string {
    return window.sessionStorage.getItem(STORAGE_KEYS_ENUM.AUTH_TOKEN) || '';
  }

  saveToken(token: string) {
    window.sessionStorage.setItem(STORAGE_KEYS_ENUM.AUTH_TOKEN, token);
  }

  deleteToken() {
    window.sessionStorage.removeItem(STORAGE_KEYS_ENUM.AUTH_TOKEN);
  }

  isTokenValid(): boolean {
    try {
      const token = this.getToken();
      return !!token && !this._helper.isTokenExpired(token);
    } catch (error) {
      this.deleteToken();
      return false;
    }
  }
}
