import { Pipe, PipeTransform } from '@angular/core';
import { EntitiesUtils } from '@aft-core';

@Pipe({
  name: 'country',
})
export class CountryPipe implements PipeTransform {
  transform(areaId?: string | number, type?: any): string {
    const country = EntitiesUtils.findCountryById(areaId);
    if (!country) {
      return 'Country not found';
    }

    switch (type) {
      case 'name':
        return country.name;
      case 'alpha2code':
        return country.alpha2code!;
      case 'alpha3code':
        return country.alpha3code!;
      default:
        return country.name;
    }
  }
}
