import { Component, OnDestroy, OnInit } from '@angular/core';
import { IPlayer, IPlayerStats, IPlayerStatsExtended, PLAYER_HIGHLIGHTS_ARRAY, PlayerService } from '@aft-core';
import { BehaviorSubject, combineLatest, Subscription } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';

type WyscoutStatSecondarySummary = {
  percentage: number;
  value: number;
  title: string;
  secondaryTitle: string;
};

@Component({
  selector: 'aft-player-wyscout-stat-secondary-summary',
  templateUrl: './player-wyscout-stat-secondary-summary.component.html',
  styleUrls: ['./player-wyscout-stat-secondary-summary.component.scss'],
})
export class PlayerWyscoutStatSecondarySummaryComponent implements OnInit, OnDestroy {
  playerDetails$: BehaviorSubject<IPlayer | undefined>;
  playerStats$: BehaviorSubject<IPlayerStats | undefined>;
  playerExtendedStats$: BehaviorSubject<Array<IPlayerStatsExtended> | undefined>;

  highlights: Array<WyscoutStatSecondarySummary> = [];

  private _subscribers: Array<Subscription> = [];

  constructor(private _playerService: PlayerService, private _translateService: TranslateService) {
    this.playerDetails$ = this._playerService.playerDetails$;
    this.playerStats$ = this._playerService.playerStats$;
    this.playerExtendedStats$ = this._playerService.playerStatsExtended$;
  }

  ngOnInit(): void {
    this._subscribers.push(
      combineLatest(this.playerDetails$, this.playerStats$).subscribe(([playerDetails, playerStats]) => {
        if (playerDetails && playerStats) {
          this._loadHighlights();
        }
      })
    );
  }

  ngOnDestroy(): void {
    this._subscribers.forEach(x => x.unsubscribe());
  }

  private _loadHighlights(): void {
    const result: Array<WyscoutStatSecondarySummary> = [];
    const roleId = this.playerDetails$.value?.roleId as number;
    // @ts-ignore
    const highlights = PLAYER_HIGHLIGHTS_ARRAY[roleId] as Array<string>;

    const playerStats = this.playerStats$.value?.playerStats;

    highlights.forEach(statName => {
      result.push();
    });

    this.highlights = result;
  }
}
