import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { IEvent, ITeamMatch, IVideo, MediaService } from '@aft-core';
import { generateFullPrivateModuleRoute as generateScoutingRoutes } from '@scouting/private/private.routes';
import { PRIVATE_MODULE_ROUTES as SCOUTING_ROUTES } from '@scouting/private/private.routes';

@Component({
  selector: 'aft-video-event',
  templateUrl: './video-event-component.html',
  styleUrls: ['./video-event-component.scss'],
})
export class VideoEventComponent implements OnInit {
  @Input()
  event: IEvent | undefined;

  @Input()
  homeTeam: ITeamMatch | undefined;

  @Input()
  awayTeam: ITeamMatch | undefined;

  @Input()
  preview: boolean = false;

  @Input()
  smallPreview: boolean = false;

  @Input()
  bigPreview: boolean = false;

  @Input()
  match: boolean = false;

  @Input()
  biggerBox: boolean = false;

  videoUrl: string = '';
  teamFlagUrl: string = '';
  display: boolean = false;

  constructor(private _router: Router, private _mediaService: MediaService) {}

  ngOnInit(): void {
    if (this.match && this.homeTeam && this.awayTeam) {
      this.teamFlagUrl = this.event?.teamId === this.homeTeam?.id ? this.homeTeam?.imageDataURL : this.awayTeam?.imageDataURL;
    }
    if (!this.event) {
      throw new Error('Event parameter not recieved by input');
    }
  }

  goToPlayerDetails(playerId: number | undefined): void {
    if (playerId) {
      this._router.navigate(generateScoutingRoutes(SCOUTING_ROUTES.PLAYER, playerId));
    }
  }

  showVideo() {
    if (this.event) {
      this._mediaService.getVideoUrl(this.event.matchId, this.event.videoTimestamp).subscribe(
        (videoData: IVideo) => {
          this.videoUrl = videoData.urlVideo;
          this.display = true;
        },
        (error: any) => {
          console.error('Error al obtener la url del video: ', error);
        }
      );
    }
  }
}
