import { Pipe, PipeTransform } from '@angular/core';
import { EntitiesUtils, IArea } from '@aft-core';

@Pipe({
  name: 'countryFlag',
})
export class CountryFlagPipe implements PipeTransform {
  transform(area?: IArea): string {
    return EntitiesUtils.findCountryImageById(area?.id);
  }
}
