import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';

@Component({
  selector: 'aft-player-rating',
  templateUrl: './player-rating.component.html',
  styleUrls: ['./player-rating.component.css'],
})
export class PlayerRatingComponent implements OnInit {
  @Input() set score(value: number | undefined) {
    this._score = value;
    this.scoreControl?.setValue(this._score);
    if (this.disabled) {
      this.scoreControl?.disable();
    }
  }

  @Input()
  disabled: boolean = true;

  @Input()
  starMode: boolean = false;

  scoreControl: FormControl = this._fb.control({});

  private _score: number | undefined = 0;

  constructor(private _fb: FormBuilder) {}

  ngOnInit(): void {
    this.scoreControl = this._fb.control({ value: this.score, disabled: this.disabled });
  }

  get score(): number {
    return this._score || 0;
  }
}
