import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { API_TYPE_ENUM } from '../../enums';
import { IDefaultResponse, IFixture, IFixtureDate, IMatch } from '../../interfaces';
import { ApiService } from '../system/api.service';
import { MessagingService } from '../ui/messaging.service';
import { ApiUtils } from '../../utils';

@Injectable({
  providedIn: 'root',
})
export class FixtureService {
  fixture$ = new BehaviorSubject<IFixture | undefined>(undefined);
  seasonDates$ = new BehaviorSubject<Array<IFixtureDate>>([]);
  matches$ = new BehaviorSubject<Array<IMatch>>([]);

  private _isDatesLoading = false;
  private _isFixtureLoading = false;
  private _isMatchesLoading = false;

  constructor(private _apiService: ApiService, private _messagingService: MessagingService) {}

  loadSeasonDates(seasonId: number): void {
    if (!this._isDatesLoading) {
      const url = `seasons/${seasonId}/fixture/dates`;

      this._isDatesLoading = true;
      this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
        (res: any) => {
          const dates = res.element.map((date: any) => {
            return this.generateDate(date);
          });
          this.seasonDates$.next(dates);
          this._isDatesLoading = false;
        },
        () => {
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
          this._isDatesLoading = false;
        }
      );
    }
  }

  loadFixtureInfoByDate(seasonId: number, dateId?: number, teamId?: number, reducedValues?: boolean): void {
    if (!this._isFixtureLoading) {
      let url = `seasons/${seasonId}/fixture`;
      if (dateId || teamId || reducedValues) {
        const params = {
          dateId,
          reduced: reducedValues,
          teamId,
        };
        url += `?${ApiUtils.objectParamSerializer(params)}`;
      }

      this._isFixtureLoading = true;
      this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
        (res: any) => {
          if (res.element) {
            this.fixture$.next(this.generateFixture(res.element));
          } else {
            this.fixture$.next(undefined);
          }
          this._isFixtureLoading = false;
        },
        () => {
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
          this.fixture$.next(undefined);
          this._isFixtureLoading = false;
        }
      );
    }
  }

  loadMatchesInfo(matchesIdList: Array<number>, reduced: boolean = false): void {
    if (!this._isMatchesLoading) {
      this._isMatchesLoading = true;
      const url = `matches?ids=${matchesIdList.toString()}&reduced=${reduced}`;

      this._apiService
        .get(API_TYPE_ENUM.LEAGUES, url)
        .pipe(map((res: IDefaultResponse) => res.element))
        .subscribe(
          (res: Array<IMatch>) => {
            this.matches$.next(res);
            this._isMatchesLoading = false;
          },
          () => {
            this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
            this.matches$.next([]);
            this._isMatchesLoading = false;
          }
        );
    }
  }

  generateDate(date: IFixtureDate): IFixtureDate {
    return {
      dateId: date.dateId,
      name: date.name,
      actual: date.actual,
    };
  }

  generateFixture(fixture: IFixture): IFixture {
    // Se convierte la fecha utc a la timezone local
    const details = fixture.details.map(detail => {
      const newDate = new Date(detail.dateutc);
      newDate.setMinutes(newDate.getMinutes() - newDate.getTimezoneOffset());
      return { ...detail, dateutc: newDate };
    });

    return {
      leagueId: fixture.leagueId,
      leagueName: fixture.leagueName,
      seasonId: fixture.seasonId,
      dateId: fixture.dateId,
      dateName: fixture.dateName,
      details,
      hasData: this.validFixtureData(fixture),
    };
  }

  validFixtureData(fixture: IFixture): boolean {
    return fixture.details.every(match => match.homeTeam && match.awayTeam);
  }

  set fixture(fixture: IFixture | undefined) {
    this.fixture$.next(fixture);
  }

  resetService(): void {
    this.fixture$.next(undefined);
    this.seasonDates$.next([]);
  }
}
