import { Pipe, PipeTransform } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EntitiesUtils } from '@aft-core';

@Pipe({
  name: 'playerRole',
})
export class PlayerRolePipe implements PipeTransform {
  constructor(private _translateService: TranslateService) {}

  transform(roleId?: string | number, type?: any): unknown {
    const role = EntitiesUtils.findRoleById(roleId);
    if (!role) {
      return 'Role not found';
    }
    switch (type) {
      case 'name':
        return this._getRoleName(role);
      case 'code2':
        return role?.code2;
      case 'code3':
        return role?.code3;
      default:
        return role?.code3;
    }
  }

  private _getRoleName(role: any): string {
    switch (role?.id) {
      case 1:
        return this._translateService.instant('PLAYER.ROLES.MID');
      case 2:
        return this._translateService.instant('PLAYER.ROLES.FW');
      case 3:
        return this._translateService.instant('PLAYER.ROLES.GK');
      case 4:
        return this._translateService.instant('PLAYER.ROLES.DF');
      default:
        return this._translateService.instant('PLAYER.ROLES.FW');
    }
  }
}
