import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { IDefaultResponse, IVideo } from '../../interfaces';
import { ApiService } from '../system/api.service';
import { API_TYPE_ENUM } from '../../enums';

@Injectable({
  providedIn: 'root',
})
export class MediaService {
  constructor(private _apiService: ApiService) {}

  getVideoUrl(matchId: number, videoTimestamp: number): Observable<IVideo> {
    const url = `videos/${matchId}/${videoTimestamp}`;
    return this._apiService.get(API_TYPE_ENUM.MEDIA, url).pipe(map((res: IDefaultResponse) => res.element));
  }
}
