import { Component } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { IPlayer, PLAYER_TRANSFER_TYPE, PLAYER_TRANSFER_TYPES, PlayerService } from '@aft-core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'aft-player-transfers',
  templateUrl: './player-transfers.component.html',
  styleUrls: ['./player-transfers.component.scss'],
})
export class PlayerTransfersComponent {
  playerDetails$: BehaviorSubject<IPlayer | undefined>;

  constructor(private _playerService: PlayerService, private _translateService: TranslateService) {
    this.playerDetails$ = this._playerService.playerDetails$;
  }

  resolveTransferClass(transferType: PLAYER_TRANSFER_TYPE): string {
    switch (transferType) {
      case PLAYER_TRANSFER_TYPES.TRANSFER:
        return 'light_blue';
      case PLAYER_TRANSFER_TYPES.BACK_FROM_LOAN:
        return 'green';
      case PLAYER_TRANSFER_TYPES.LOAN:
        return 'dark_blue';
      default:
        return '';
    }
  }

  resolverTransferText(transferType: PLAYER_TRANSFER_TYPE): string {
    switch (transferType) {
      case PLAYER_TRANSFER_TYPES.TRANSFER:
        return this._translateService.instant('PLAYER.TRANSFERS_TABLE.TRANSFER_TYPES.TRANSFER');
      case PLAYER_TRANSFER_TYPES.BACK_FROM_LOAN:
        return this._translateService.instant('PLAYER.TRANSFERS_TABLE.TRANSFER_TYPES.BACK_FROM_LOAN');
      case PLAYER_TRANSFER_TYPES.LOAN:
        return this._translateService.instant('PLAYER.TRANSFERS_TABLE.TRANSFER_TYPES.LOAN');
      case PLAYER_TRANSFER_TYPES.FREE_TRANSFER:
        return this._translateService.instant('PLAYER.TRANSFERS_TABLE.TRANSFER_TYPES.FREE_TRANSFER');
      default:
        return this._translateService.instant('PLAYER.TRANSFERS_TABLE.TRANSFER_TYPES.NOT_AVAILABLE');
    }
  }
}
