import { IPagination } from '../interfaces';

export class ApiUtils {
  static objectParamSerializer(object: any): string {
    let str = '';
    const keys = Object.keys(object);
    // eslint-disable-next-line no-restricted-syntax
    for (const key of keys) {
      const value = object[key];
      if (value || value === 0) {
        if (str !== '') {
          str += '&';
        }
        if (object[key] !== undefined) {
          str += `${key}=${encodeURIComponent(object[key])}`;
        }
      }
    }
    return str;
  }

  static createPaginationObject(paginationObj?: IPagination): IPagination {
    const pageSize = paginationObj?.pageSize || 20;
    return {
      offset: paginationObj ? (paginationObj.currentPage - 1) * pageSize : 0,
      currentPageItems: paginationObj?.currentPageItems || 20,
      totalElements: paginationObj?.totalElements || 0,
      totalPages: paginationObj?.totalPages || 0,
      currentPage: paginationObj?.currentPage || 0,
      pageSize,
    };
  }
}
