import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BehaviorSubject, Subscription, merge } from 'rxjs';
import {
  ILeague,
  IPlayer,
  IPlayerRanking,
  IPlayerRole,
  LeaguesService,
  PLAYER_ROLES_ARRAY,
  PlayerService,
  STORAGE_KEYS_ENUM,
} from '@aft-core';
import { FormBuilder, FormControl } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'aft-player-ranking',
  templateUrl: './player-ranking.component.html',
  styleUrls: ['./player-ranking.component.scss'],
})
export class PlayerRankingComponent implements OnInit, OnDestroy {
  @Input()
  league$: BehaviorSubject<ILeague | undefined>;

  @Input()
  playerUrl: Array<string | number> = [];

  ranking$: BehaviorSubject<IPlayerRanking[]>;

  roles = PLAYER_ROLES_ARRAY;

  scoreColumnName: string = 'GOALS';

  private _storageKey = STORAGE_KEYS_ENUM.HOME_PLAYER_ROLE;
  private readonly _positionControl: FormControl = this._fb.control(this.roles[0]);
  private _subscriptions: Subscription[] = [];

  constructor(private _fb: FormBuilder, private _leagueService: LeaguesService, private _router: Router) {
    // This behaviour subjects gets rewritten on the onInit lifecycle when sent as input
    this.league$ = new BehaviorSubject<ILeague | undefined>(undefined);
    this.ranking$ = this._leagueService.ranking$;
  }

  ngOnInit(): void {
    const localStorageVal = localStorage.getItem(this._storageKey);
    if (localStorageVal) {
      const role = this.roles.find(r => r.id === +localStorageVal);
      if (role) {
        this._positionControl.setValue(role);
      }
    }

    this._subscriptions.push(
      merge(this.league$, this._positionControl.valueChanges).subscribe(() => {
        const league = this.league$.value;
        const role = this._positionControl.value;

        if (league && role) {
          localStorage.setItem(this._storageKey, role.id.toString());

          this._leagueService.getLeagueRanking(league.id, role.id);

          switch (role) {
            case PLAYER_ROLES_ARRAY[0]:
              this.scoreColumnName = 'SUCCESSFUL_PASSES';
              break;
            case PLAYER_ROLES_ARRAY[1]:
              this.scoreColumnName = 'GOALS';
              break;
            case PLAYER_ROLES_ARRAY[2]:
              this.scoreColumnName = 'SAVES';
              break;
            case PLAYER_ROLES_ARRAY[3]:
              this.scoreColumnName = 'DUELS_WON';
              break;
            default:
              this.scoreColumnName = 'GOALS';
              break;
          }
        }
      })
    );
  }

  scoreResolver(ranking: IPlayerRanking): number {
    const role = this._positionControl.value;

    switch (role) {
      case PLAYER_ROLES_ARRAY[0]:
        return ranking.successfulPasses;
      case PLAYER_ROLES_ARRAY[1]:
        return ranking.goals;
      case PLAYER_ROLES_ARRAY[2]:
        return ranking.gkSaves;
      case PLAYER_ROLES_ARRAY[3]:
        return ranking.duelsWon;
      default:
        return ranking.goals;
    }
  }

  goToPlayerDetails(player: IPlayerRanking): void {
    if (this.playerUrl.length) {
      this._router.navigate([...this.playerUrl, player.playerId]);
    } else {
      throw new Error('Player route not defined');
    }
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  get positionCtrl(): FormControl {
    return this._positionControl;
  }
}
