export interface IStandings {
  leagueId: number;
  leagueName: string;
  seasonId: number;
  seasonName: string;
  tables: Array<ITable>;
}

export interface ITable {
  roundName: string;
  roundOrder: number;
  teams: Array<ITeamsStandings>;
}

export interface ITeamsStandings {
  teamId: number;
  teamName: string;
  teamImageURL: string;
  totalPoints: number;
  totalPlayed: number;
  totalWins: number;
  totalDraws: number;
  totalLosses: number;
  totalGoalsFor: number;
  totalGoalsAgainst: number;
}
