import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';

import { ApiService } from '../system/api.service';
import { API_TYPE_ENUM, ENTITIES_ENUM } from '../../enums';
import { IEditUserRequest, IUser, IUserResponse, IUserFavorites } from '../../interfaces';
import { MessagingService } from '../ui/messaging.service';

@Injectable()
export class UserService {
  loadedProfile$ = new BehaviorSubject<IUser | undefined>(undefined);
  favorites$ = new BehaviorSubject<IUserFavorites | undefined>(undefined);

  private _isLoading: boolean = false;

  constructor(private _apiService: ApiService, private _messagingService: MessagingService) {}

  private _handleLoadProfile = (res: IUserResponse): void => {
    this.loadedProfile$.next(res.element);
  };

  isFavorite(id: number, entity: string): boolean {
    const favorites = this.favorites$.value;

    switch (entity) {
      case ENTITIES_ENUM.LEAGUE: {
        return !!favorites?.leagues?.find(x => x.id === id);
      }
      case ENTITIES_ENUM.TEAM: {
        return !!favorites?.teams?.find(x => x.id === id);
      }
      case ENTITIES_ENUM.PLAYER:
      default: {
        return !!favorites?.players?.find(x => x.id === id);
      }
    }
  }

  deleteFavorite(id: number, entity: string): void {
    const url = `favorite?entityType=${entity.toLowerCase()}&entityId=${id}`;
    this._apiService
      .delete(API_TYPE_ENUM.LEAGUES, url)
      .pipe(map(res => res.element))
      .subscribe(
        () => {
          this.getFavorites();
        },
        () => {
          this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
        }
      );
  }

  addFavorite(id: number, entityType: string): void {
    const url = `favorite`;
    this._apiService
      .post(API_TYPE_ENUM.LEAGUES, url, { entityId: id, entityType: entityType.toLowerCase() })
      .pipe(map(res => res.element))
      .subscribe(
        () => {
          this.getFavorites();
        },
        () => {
          this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
        }
      );
  }

  getFavorites(): void {
    const url = `favorite`;
    this._apiService
      .get(API_TYPE_ENUM.LEAGUES, url)
      .pipe(map(res => res.element))
      .subscribe(
        res => {
          this.favorites$.next(res);
        },
        () => {
          this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
        }
      );
  }

  getBackofficeProfile(): void {
    if (!this._isLoading) {
      const url = `admins/profile`;
      this._apiService.get(API_TYPE_ENUM.IDENTITY, url).subscribe(this._handleLoadProfile, () => {
        this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
      });
    }
  }

  getScoutProfile(): void {
    if (!this._isLoading) {
      const url = `scouts/profile`;
      this._apiService.get(API_TYPE_ENUM.IDENTITY, url).subscribe(this._handleLoadProfile, () => {
        this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
      });
    }
  }

  setBackofficeProfile(profile: IEditUserRequest): void {
    const url = `admins/profile`;
    this._apiService.put(API_TYPE_ENUM.IDENTITY, url, profile).subscribe(this._handleLoadProfile, () => {
      this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
    });
  }

  setScoutProfile(profile: IEditUserRequest): void {
    const url = `scouts/profile`;
    this._apiService.put(API_TYPE_ENUM.IDENTITY, url, profile).subscribe(this._handleLoadProfile, () => {
      this._messagingService.error('PROFILE.TOAST.ERROR.TITLE', 'PROFILE.TOAST.ERROR.MESSAGE');
    });
  }

  hasAlgorithmPermission(): boolean {
    return this.loadedProfile$.value?.permissions?.algorithm || false;
  }

  hasImportsPermission(): boolean {
    return this.loadedProfile$.value?.permissions?.imports || false;
  }

  hasInvitesPermission(): boolean {
    return this.loadedProfile$.value?.permissions?.invites || false;
  }

  hasPaymentsPermission(): boolean {
    return this.loadedProfile$.value?.permissions?.payments || false;
  }

  hasMassiveEditPermission(): boolean {
    return this.loadedProfile$.value?.permissions?.players || false;
  }

  hasUsersPermission(): boolean {
    return this.loadedProfile$.value?.permissions?.users || false;
  }
}
