import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpHandler, HttpRequest } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { AuthService, MessagingService } from '../services';

@Injectable()
export class SessionInterceptor implements HttpInterceptor {
  private _urlWhitelistExclusions = ['/sessions/scoutGoogle', '/sessions/scouting', '/sessions/backoffice'];

  constructor(private _authService: AuthService, private _messagingService: MessagingService) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<any> {
    return next.handle(request).pipe(
      catchError(error => {
        if (error.status === 401) {
          // Redirects users to home if not logged in or auth token is invalid
          const formattedUrl = error.url?.replace('http://', '');
          if (formattedUrl) {
            const urlWhiteList = this._urlWhitelistExclusions.find(x => formattedUrl.includes(x));
            if (!urlWhiteList) {
              this._messagingService.error('GENERAL.ERRORS.SESSION_EXPIRED.TITLE', 'GENERAL.ERRORS.SESSION_EXPIRED.MESSAGE');
              this._authService.logout();
            }
          }
        }
        return throwError(error);
      })
    );
  }
}
