export type KeysWithStringType<T extends {}> = { [K in keyof T]-?: T[K] extends string ? K : never }[keyof T];
export type KeysWithNumberType<T extends {}> = {
  [K in keyof T]-?: T[K] extends number ? K : never;
}[keyof T];

// Only make available the keys of the object that are of serializable type, since the function will require a string to be passed
export type KeysWithSerializableType<T extends {}> = KeysWithStringType<T> | KeysWithNumberType<T>;
