import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IAnalystDataRequest, IPlayer, IPlayerStats, IPlayerStatsExtended, IPlayerUpdateRequest } from '../../interfaces';
import { ApiService } from '../system/api.service';
import { MessagingService } from '../ui/messaging.service';
import { API_TYPE_ENUM } from '../../enums';

@Injectable({
  providedIn: 'root',
})
export class PlayerService {
  playerDetails$ = new BehaviorSubject<IPlayer | undefined>(undefined);
  playerStats$ = new BehaviorSubject<IPlayerStats | undefined>(undefined);
  playerStatsExtended$ = new BehaviorSubject<Array<IPlayerStatsExtended> | undefined>(undefined);

  playerDetailsParams = {
    CAREER: 'career',
    TRANSFERS: 'transfers',
    OBSERVATIONS: 'observations',
    HIGHLIGHTS: 'highlights',
    POSITIONS: 'positions',
  };

  constructor(private _apiService: ApiService, private _messagingService: MessagingService) {}

  private _handleLoadPlayerDetails = (res: any): void => {
    this.playerDetails$.next(res.element);
  };

  getPlayerDetailsExtended(playerId: string, details?: Array<string>): void {
    let url = `players/details/${playerId}`;
    if (details?.length) {
      url += `?details=${details}`;
    }

    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(this._handleLoadPlayerDetails, () => {
      this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
    });
  }

  getPlayerDetails(playerId: string, details?: Array<string>): void {
    if (!this.playerDetails$.value || this.playerDetails$.value.id !== +playerId) {
      let url = `players/details/${playerId}`;
      if (details?.length) {
        url += `?details=${details}`;
      }
      this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(this._handleLoadPlayerDetails, () => {
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
      });
    }
  }

  getPlayerStats(playerId?: number | undefined): void {
    let id = playerId;
    if (!playerId) {
      id = this.playerDetails$.value?.id;
    }
    if (!id) {
      throw new Error('Could not fetch player stats, player id not found');
    }

    const url = `playerstats/${id}`;
    this._apiService
      .get(API_TYPE_ENUM.STATS, url)
      .pipe(
        map(res => {
          return res.element;
        })
      )
      .subscribe(
        (playerStats: IPlayerStats) => {
          this.playerStats$.next(playerStats);
        },
        () => {
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      );
  }

  getPlayerExtendedStats(playerId: number, seasonId: number): void {
    const url = `playerstats/${playerId}/matchstats/${seasonId}`;
    this._apiService
      .get(API_TYPE_ENUM.STATS, url)
      .pipe(
        map(res => {
          return res.element;
        })
      )
      .subscribe(
        (playerStats: Array<IPlayerStatsExtended>) => {
          this.playerStatsExtended$.next(playerStats);
        },
        () => {
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      );
  }

  savePlayerStats(statsData: Array<IAnalystDataRequest>): void {
    this._apiService.post(API_TYPE_ENUM.STATS, 'analysts', statsData).subscribe(
      () => {
        this._messagingService.success(
          'PLAYER_STATS_DETAIL.TOAST.SAVE_STATS.TITLE',
          'PLAYER_STATS_DETAIL.TOAST.SAVE_STATS.MESSAGE'
        );
        this.getPlayerStats();
      },
      () => {
        this._messagingService.error('PLAYER_STATS_DETAIL.TOAST.ERRORS.TITLE', 'MASSIVE_EDIT.TOAST.ERRORS.MESSAGE');
      }
    );
  }

  updatePlayer(playerData: IPlayerUpdateRequest, id: string | undefined): Observable<any> {
    return this._apiService.put(API_TYPE_ENUM.LEAGUES, `players/details/${id}`, playerData);
  }

  playerContactInfo(firstName: string, lastName: string, team: string): void {
    const body = {
      firstName,
      lastName,
      team,
    };
    this._apiService.post(API_TYPE_ENUM.IDENTITY, 'scouts/contactPlayer', body).subscribe(
      () => {
        this._messagingService.success('PLAYER.CONTACT.TOAST.SUCCESS.TITLE', 'PLAYER.CONTACT.TOAST.SUCCESS.SUBTITLE');
      },
      () => {
        this._messagingService.error('PLAYER.CONTACT.TOAST.ERROR.TITLE', 'PLAYER.CONTACT.TOAST.ERROR.SUBTITLE');
      }
    );
  }
}
