import { Injectable } from '@angular/core';
import { Router, CanActivateChild } from '@angular/router';
import { AuthService, MessagingService, TokenService } from '../services';
import { APP_ROUTES } from '../app.routes';

@Injectable()
export class PrivateCanActivateChildGuard implements CanActivateChild {
  constructor(
    private _router: Router,
    private _authService: AuthService,
    private _tokenService: TokenService,
    private _messagingService: MessagingService
  ) {}

  canActivateChild(): boolean {
    const token = this._tokenService.getToken();
    if (!token) {
      this._messagingService.error('GENERAL.ERRORS.SESSION_EXPIRED.TITLE', 'GENERAL.ERRORS.SESSION_EXPIRED.TITLE');
      this._authService.logout();
      this._router.navigate(APP_ROUTES.LANDING);
      return false;
    }
    return true;
  }
}
