/* eslint-disable max-lines */
import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { Validators } from '@angular/forms';
import { IAdvSearchRequest, IPagination, IPlayer, IPlayerRole, ITeam } from '../../interfaces';
import { ApiUtils } from '../../utils';
import { ApiService } from '../system/api.service';
import { MessagingService } from '../ui/messaging.service';
import { API_TYPE_ENUM } from '../../enums';

@Injectable({
  providedIn: 'root',
})
export class AdvancedSearchService {
  players$ = new BehaviorSubject<IPlayer[]>([]);
  pagination$ = new BehaviorSubject<IPagination>(ApiUtils.createPaginationObject());

  sortOptions: Array<any>;

  private _formValues: any;
  private _initialAgeRange = [15, 60];
  private _initialHeightRange = [130, 220];
  private _initialWeightRange = [50, 90];

  constructor(
    private _apiService: ApiService,
    private _messagingService: MessagingService,
    private _translateService: TranslateService
  ) {
    this.sortOptions = [
      {
        name: this._translateService.instant('PLAYERS_ADVANCED_SEARCH.SORTING.NEWEST'),
        value: 'CreationDate',
      },
    ];
    this._formValues = this.resetFilterValues();
  }

  set formValues(formValues: any) {
    this._formValues = formValues;
  }

  resetFilterValues(): Object {
    return {
      league: null,
      playerName: '',
      score: null,
      favorites: false,
      playerRoles: null,
      teams: null,
      foot: null,
      age: this._initialAgeRange,
      height: this._initialHeightRange,
      weight: this._initialWeightRange,
      country: null,
      passport: null,
      sortBy: this.sortOptions[0].value,
    };
  }

  loadFilterValues(): Object {
    return {
      league: [this._formValues?.league || undefined],
      playerName: [this._formValues?.playerName || '', Validators.minLength(2)],
      score: [this._formValues?.score || null],
      favorites: [{ value: this._formValues?.favorites || false, disabled: true }],
      playerRoles: [this._formValues?.playerRoles || null],
      teams: [{ value: this._formValues?.teams || null, disabled: !this._formValues?.league }],
      foot: [this._formValues?.foot || null],
      age: [this._formValues?.age || this._initialAgeRange],
      height: [this._formValues?.height || this._initialHeightRange],
      weight: [this._formValues?.weight || this._initialWeightRange],
      country: [this._formValues?.country || undefined],
      passport: [this._formValues?.passport || undefined],
      sortBy: [this.sortOptions[0].value],
    };
  }

  doSearch(offset?: number): void {
    let heightRange;
    let weightRange;
    let ageRange;

    if (this._formValues.height !== this._initialHeightRange) {
      heightRange = this._formValues.height?.toString();
    }
    if (this._formValues.age !== this._initialAgeRange) {
      ageRange = this._formValues.age?.toString();
    }
    if (this._formValues.weight !== this._initialWeightRange) {
      weightRange = this._formValues.weight?.toString();
    }

    const optionalParams = {
      searchText: this._formValues.playerName?.length > 1 ? this._formValues.playerName : undefined,
      favorites: this._formValues.favorites,
      passportId: this._formValues.passport?.id,
      birthAreaId: this._formValues.country?.id,
      ageRange,
      heightRange,
      weightRange,
      rolesIds: this._formValues.playerRoles?.map((rol: IPlayerRole) => rol.id).toString() || undefined,
      leagueId: this._formValues.league?.id,
      teamsIds: this._formValues.teams?.map((team: ITeam) => team.id)?.toString() || undefined,
      foot: this._formValues.foot?.map((foot: any) => foot.name)?.toString() || undefined,
      score: this._formValues.score,
    };

    const formattedOptionalParams = ApiUtils.objectParamSerializer(optionalParams);

    if (formattedOptionalParams?.length) {
      const searchParams: IAdvSearchRequest = {
        ...optionalParams,
        offset: offset || 0,
        sortBy: this._formValues.sortBy,
      };

      const formattedParams = ApiUtils.objectParamSerializer(searchParams);
      const url = `players/?${formattedParams}`;
      this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
        res => {
          this.players$.next(res.element?.elements);
          this.pagination$.next(ApiUtils.createPaginationObject(res.element));
        },
        () => {
          this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        }
      );
    } else {
      this._messagingService.info(
        'PLAYERS_ADVANCED_SEARCH.ERRORS.NO_PARAMS_TITLE',
        'PLAYERS_ADVANCED_SEARCH.ERRORS.NO_PARAMS_MESSAGE'
      );
    }
  }
}
