import { ILanguage } from './language';

export interface IUser {
  id?: string;
  firstName: string;
  lastName: string;
  role?: string;
  email: string;
  phoneNumber?: string;
  imageURL?: string;
  country?: string;
  city?: string;
  photoUrl?: string;
  organization?: string;
  webSite?: string;
  workplace?: string;
  linkedinProfileURL?: string;
  description?: string;
  languages?: Array<ILanguage>;
  status?: string;
  creationDate?: Date;
  profession?: string;
  permissions?: IUserPermissions;
}

export interface IUserResponse {
  element: IUser;
  message: string;
}

export interface IUserPermissions {
  users?: boolean;
  algorithm?: boolean;
  imports?: boolean;
  invites?: boolean;
  payments?: boolean;
  players?: boolean;
}

export interface IEditUserRequest {
  firstName: string;
  lastName: string;
  phoneNumber: string;
  imageURL?: string;
  role?: string;
  country: string;
  city: string;
  email?: string;
  languages?: Array<ILanguage>;
}

export interface ICreateUserRequest {
  firstName: string;
  lastName: string;
  password: string;
  phoneNumber: string;
  email: string;
}
