import { Component, ElementRef, Input, NgZone, OnInit, ViewChild } from '@angular/core';
import { IPayment, PaymentService } from '@aft-core';

declare global {
  interface Window {
    paypal: any;
  }
}

window.paypal = window.paypal || {};

@Component({
  selector: 'aft-paypal-component',
  templateUrl: './paypal-component.html',
  styleUrls: ['./paypal-component.scss'],
})
export class PayPalComponent implements OnInit {
  @Input()
  amountValue: string | undefined;

  @Input()
  userEmail: string | undefined;

  @ViewChild('paypalRef', { static: true }) private paypalRef: ElementRef | undefined;

  constructor(private _paymentService: PaymentService, private _ngZone: NgZone) {}

  ngOnInit(): void {
    if (!this.userEmail || !this.amountValue) {
      throw new Error('User email or Amount parameter not recieved by input');
    }
    window.paypal
      .Buttons({
        style: {
          layout: 'horizontal',
          tagline: 'false',
        },
        createOrder: (data: any, actions: any) => {
          return this._ngZone.run(() => this._createOrder(data, actions));
        },
        onApprove: (data: any, actions: any) => {
          return this._ngZone.run(() => this._onApprove(data, actions));
        },
        onError: (err: any) => {
          return this._ngZone.run(() => this._onError(err));
        },
      })
      .render(this.paypalRef?.nativeElement);
  }

  private _createOrder(data: any, actions: any): void {
    return actions.order.create({
      purchase_units: [
        {
          amount: {
            value: this.amountValue,
          },
        },
      ],
    });
  }

  private _onApprove(data: any, actions: any): void {
    const payment: IPayment = {
      orderID: data.orderID,
      payerID: data.payerID,
      paymentID: data.paymentID,
      billingToken: data.billingToken,
      facilitatorAccessToken: data.facilitatorAccessToken,
      userEmail: this.userEmail,
    };

    this._paymentService.savePayment(payment);
    return actions.order.capture();
  }

  private _onError(err: any): void {
    console.error('ERROR: ', err);
    this._paymentService.paymentSuccessful$.next(false);
  }
}
