/* eslint-disable @typescript-eslint/no-unused-vars */
import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { APP_ROUTES } from '../app.routes';
import { UserService } from '../services';

@Injectable()
export class ImportsGuard implements CanActivate {
  constructor(private _router: Router, private _userService: UserService) {}

  canActivate(route: ActivatedRouteSnapshot, router: RouterStateSnapshot): Observable<any> | Promise<any> | boolean | UrlTree {
    const hasPermission = this._userService.hasImportsPermission();
    if (!hasPermission) {
      this._router.navigate(APP_ROUTES.LANDING);
    }

    return hasPermission;
  }
}
