import { IMatch } from './match';

export interface IFixture {
  dateId: number;
  dateName: string;
  details: IFixtureMatch[];
  leagueId: number;
  leagueName: string;
  seasonId: number;
  hasData: boolean;
}

export interface IFixtureScoreDetails {
  period: string;
  minute: number;
  playerId: number;
  playerName: string;
  teamId: number;
  teamName: string;
}

export interface IFixtureMatchCard {
  period: string;
  minute: number;
  playerId: number;
  playerName: string;
  teamId: number;
  teamName: string;
}
export interface IFixtureMatch extends IMatch {
  label: string;
  duration: string;
  yellowCardInfo: Array<IFixtureMatchCard>;
  redCardInfo: Array<IFixtureMatchCard>;
}

export interface IFixtureDate {
  dateId: number;
  name: string;
  actual: boolean;
}
