import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { AuthService, TokenService } from '../services';
import { APP_ROUTES } from '../app.routes';

@Injectable()
export class PrivateGuard implements CanActivate {
  constructor(private _authService: AuthService, private _router: Router, private _tokenService: TokenService) {}

  canActivate(route: ActivatedRouteSnapshot, router: RouterStateSnapshot): Observable<any> | Promise<any> | boolean | UrlTree {
    return this._authService.isLoggedIn$.pipe(
      take(1),
      map(isAuth => {
        if (isAuth && this._tokenService.isTokenValid()) {
          return true;
        }
        return this._router.navigate(APP_ROUTES.LANDING);
      })
    );
  }
}
