import { IArea } from './area';
import { ILeague } from './leagues';

export interface ITeam {
  id: number;
  wyscoutId?: number;
  name: string;
  officialName?: string;
  city?: string;
  type?: string;
  category?: string;
  gender: string;
  wsAreaId?: number;
  area?: IArea;
  imageDataURL?: string;
  leagueId?: number;
  league?: ILeague;
  creationDate?: string;
  modificationDate?: string;
}

export interface ITeamInfo {
  id: number;
  imageDataURL: string;
  leagueId: number;
  name: string;
  officialName: string;
  playerCount: number;
  seasonStandings: Array<ITeamSeasonStanding>;
}

export interface ITeamSeasonStanding {
  name: string;
  totalScore: number;
  teamStanding: number;
}
