import { Pipe, PipeTransform } from '@angular/core';
import { EntitiesUtils } from '@aft-core';

@Pipe({
  name: 'userPlatform',
})
export class UserPlatformPipe implements PipeTransform {
  transform(roleName?: string): unknown {
    const platform = EntitiesUtils.findPlatformByRole(roleName);
    if (!platform) {
      return 'No Platform';
    }
    return platform.name;
  }
}
