import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserTestingModule } from '@angular/platform-browser/testing';
import { HttpClientTestingModule } from '@angular/common/http/testing';
import { RouterTestingModule } from '@angular/router/testing';
import { TranslateModule } from '@ngx-translate/core';

import { SocialAuthService, SocialLoginModule } from 'angularx-social-login';

import { ApiService, AppConfigService, AuthService, LoadingService, MessagingService, TokenService } from '../../lib/services';
import { AppConfigServiceMock } from '../mocks/app-config.service.mock';
import { BlankComponent } from '../mocks/blank-component/blank-component.component';
import { PrivateCanActivateChildGuard, PrivateGuard, PublicGuard } from '../../lib/guards';
import { SocialAuthServiceMock } from '../mocks/social-auth.service.mock';

@NgModule({
  declarations: [BlankComponent],
  imports: [
    RouterTestingModule,
    HttpClientTestingModule,
    BrowserTestingModule,
    ReactiveFormsModule,
    TranslateModule.forRoot(),
    SocialLoginModule,
  ],
  exports: [RouterTestingModule, HttpClientTestingModule, BrowserTestingModule, ReactiveFormsModule, TranslateModule],
  providers: [
    ApiService,
    AuthService,
    {
      provide: AppConfigService,
      useClass: AppConfigServiceMock,
    },
    TokenService,
    MessagingService,
    LoadingService,
    PublicGuard,
    PrivateGuard,
    PrivateCanActivateChildGuard,
    {
      provide: SocialAuthService,
      useClass: SocialAuthServiceMock,
    },
  ],
})
export class SharedTestModule {}
