import { IAnalystStats, IArea, ILanguage, IPlayerRole } from '../interfaces';

import { COUNTRIES_ENUM_ARRAY, LANGUAGES_OPTIONS, PLATFORMS_OPTIONS, PLAYER_ROLES_ARRAY } from '../enums';
import { IPlatform } from '../interfaces/business/platform';

export class EntitiesUtils {
  static findCountryById(areaId?: string | number): IArea | undefined {
    return areaId ? COUNTRIES_ENUM_ARRAY.find(area => +area.id === +areaId) : undefined;
  }

  static findCountryImageById(areaId: string | number | undefined): string {
    const defaultCountryFlag = 'assets/images/pictures/countries/flag_default.png';
    if (!areaId) {
      return defaultCountryFlag;
    }
    return COUNTRIES_ENUM_ARRAY.find(area => +area.id === +areaId)?.photoUrl || defaultCountryFlag;
  }

  static findLangByName(name: string): ILanguage | undefined {
    return LANGUAGES_OPTIONS.find(lang => lang.name.toLowerCase() === name.toLowerCase());
  }

  static findRoleById(roleId?: string | number): IPlayerRole | undefined {
    return roleId ? PLAYER_ROLES_ARRAY.find(role => role.id === +roleId) : undefined;
  }

  static findPlatformByRole(roleName?: string): IPlatform | undefined {
    return PLATFORMS_OPTIONS.find(p => p.value.toLowerCase() === roleName?.toLowerCase());
  }

  static newAnalystStat(): IAnalystStats {
    return {
      speed: 0,
      reaction: 0,
      form: 0,
      strength: 0,
      injuries: 0,
      reflexes: 0,
      agility: 0,
      airDomain: 0,
      intensity: 0,
      technique: 0,
      polyfunctionality: 0,
      oneVsOne: 0,
      goalKick: 0,
      feetGame: 0,
      passReceptionTechnique: 0,
      penaltySave: 0,
      markOneVsOne: 0,
      positioning: 0,
      airGame: 0,
      pressingIntensity: 0,
      defendingCollaboration: 0,
      dribblingOneVsOne: 0,
      attackingCollaboration: 0,
      cornersIndirectFreeKicks: 0,
      shotDefinition: 0,
      directFreeKicks: 0,
      penalties: 0,
      mobility: 0,
      gameVision: 0,
      ferocity: 0,
      regularity: 0,
      professionalism: 0,
      leadership: 0,
      pressureManagement: 0,
      fairPlay: 0,
      concentration: 0,
      decisionMaking: 0,
      improvisation: 0,
    };
  }

  static getKeyValue =
    (key: string) =>
    (obj: Record<string, any>): number =>
      obj[key];
}
