import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { API_TYPE_ENUM, STATUS_ENTITIES_ENUM } from '../../enums';
import { IDefaultResponse, IEntityStatus, ILeague, IPlayer, ITeam, ITeamInfo } from '../../interfaces';
import { ApiService } from '../system/api.service';
import { MessagingService } from '../ui/messaging.service';
import { LeaguesService } from './leagues.service';
import { SyncStatusService } from './sync-status.service';

@Injectable({
  providedIn: 'root',
})
export class TeamsPlayersService {
  isLoading$ = new BehaviorSubject<boolean>(false);
  selectedLeague$ = new BehaviorSubject<ILeague | undefined>(undefined);
  leagues$ = new BehaviorSubject<Array<ILeague>>([]);
  teamInfo$ = new BehaviorSubject<ITeamInfo | undefined>(undefined);
  teams$ = new BehaviorSubject<Array<ITeam>>([]);
  players$ = new BehaviorSubject<Array<IPlayer>>([]);
  teamsAndPlayersStatus$ = new BehaviorSubject<IEntityStatus | undefined>(undefined);

  constructor(
    private _apiService: ApiService,
    private _leaguesService: LeaguesService,
    private _messagingService: MessagingService,
    private _syncStatusService: SyncStatusService
  ) {
    this.isLoading$ = this._syncStatusService.isLoading$;
    this.selectedLeague$ = this._leaguesService.selectedLeague$;
  }

  loadTeamInfoById = (teamId: number): void => {
    this.isLoading$.next(true);
    const url = `teams/${teamId}`;
    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
      (res: IDefaultResponse) => {
        this.teamInfo$.next(res?.element || []);
        this.isLoading$.next(false);
      },
      () => {
        this.teamInfo$.next(undefined);
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        this.isLoading$.next(false);
      }
    );
  };

  loadTeamsByLeagueId = (leagueId: number): void => {
    this.isLoading$.next(true);
    const url = `leagues/${leagueId}/teams`;
    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
      (res: IDefaultResponse) => {
        this.teams$.next(res?.element || []);
        this.isLoading$.next(false);
      },
      () => {
        this.teams$.next([]);
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        this.isLoading$.next(false);
      }
    );
  };

  loadPlayersByLeagueId = (leagueId: number): void => {
    const url = `leagues/${leagueId}/players`;
    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
      (res: IDefaultResponse) => {
        this.players$.next(res?.element || []);
      },
      () => {
        this.players$.next([]);
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
      }
    );
  };

  loadPlayersByTeamId(teamId: number): void {
    this.isLoading$.next(true);
    const url = `players/teams?ids=${teamId}`;
    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
      (res: IDefaultResponse) => {
        this.players$.next(res.element);
        this.isLoading$.next(false);
      },
      () => {
        this.players$.next([]);
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
        this.isLoading$.next(false);
      }
    );
  }

  resetTeamsAndPlayers(): void {
    this.teams$.next([]);
    this.players$.next([]);
    this.teamsAndPlayersStatus$.next(undefined);
  }

  doSyncTeamsAndPlayers(): Observable<boolean> {
    return this._syncStatusService.triggerEntitySyncStatus(STATUS_ENTITIES_ENUM.TEAMSANDPLAYERS, this.selectedLeague$.value?.id);
  }

  getTeamsAndPlayersSyncStatus = (): void => {
    this._syncStatusService
      .getEntitySyncStatus(STATUS_ENTITIES_ENUM.TEAMSANDPLAYERS, this.selectedLeague$.value?.id)
      .subscribe((result: IEntityStatus) => {
        this.teamsAndPlayersStatus$.next(result);
      });
  };
}
