import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import {
  ANALYST_STATS_GROUPS,
  EntitiesUtils,
  IAnalystDataRequest,
  IAnalystStats,
  IPlayer,
  IPlayerStats,
  IUser,
  MessagingService,
  PLAYER_ANALYST_STATS_GROUPED,
  PlayerService,
  UserService,
} from '@aft-core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { IPlayerAnalystStatList } from '../interfaces/player-analyst-stats.interface';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'aft-player-analyst-stats',
  templateUrl: './player-analyst-stats.component.html',
  styleUrls: ['./player-analyst-stats.component.scss'],
})
export class PlayerAnalystStatsComponent implements OnInit, OnDestroy {
  @Input()
  editMode: boolean;

  @Input()
  showPlayerResume: boolean = true;

  analystProfile$: BehaviorSubject<IUser | undefined>;
  playerDetails$: BehaviorSubject<IPlayer | undefined>;
  playerStats$: BehaviorSubject<IPlayerStats | undefined>;

  form: FormGroup = new FormGroup({});

  physicalStatList: Array<IPlayerAnalystStatList> = [];
  tacticsStatList: Array<IPlayerAnalystStatList> = [];
  mentalStatList: Array<IPlayerAnalystStatList> = [];

  statGroups = ANALYST_STATS_GROUPS;

  private _subscriptions: Array<Subscription> = [];

  constructor(
    private _playerService: PlayerService,
    private _fb: FormBuilder,
    private _messagingService: MessagingService,
    private _userService: UserService,
    private _translateService: TranslateService,
    private _confirmationService: ConfirmationService
  ) {
    this.analystProfile$ = this._userService.loadedProfile$;
    this.playerDetails$ = this._playerService.playerDetails$;
    this.playerStats$ = this._playerService.playerStats$;

    this.editMode = false;
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this.playerStats$.subscribe(playerStats => {
        if (playerStats) {
          let analystStats = playerStats.analystData;
          if (!analystStats) {
            analystStats = EntitiesUtils.newAnalystStat();
          }

          this.form = this._createForm(analystStats);

          this.physicalStatList = this._createStatList(ANALYST_STATS_GROUPS.PHYSICAL);
          this.tacticsStatList = this._createStatList(ANALYST_STATS_GROUPS.TACTICS);
          this.mentalStatList = this._createStatList(ANALYST_STATS_GROUPS.MENTAL);

          this.form.disable();
        } else {
          this.form = this._fb.group({});
        }
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(s => s.unsubscribe());
  }

  toggleEdition(): void {
    const isEnabled = !this.form.enabled;
    if (isEnabled) {
      this.form.enable();
    } else {
      this.form.disable();
    }
  }

  cancelChanges(): void {
    if (!this.form.pristine) {
      this._confirmationService.confirm({
        header: this._translateService.instant('GENERAL.MODAL.CONFIRM_CANCEL.TITLE'),
        message: this._translateService.instant('GENERAL.MODAL.CONFIRM_CANCEL.MESSAGE'),
        acceptLabel: this._translateService.instant('GENERAL.MODAL.CONFIRM_CANCEL.CANCEL_BTN'),
        rejectLabel: this._translateService.instant('GENERAL.MODAL.CONFIRM_CANCEL.ACCEPT_BTN'),
        accept: () => {
          this.playerStats$.next(this.playerStats$.value);
        },
      });
    } else {
      this.playerStats$.next(this.playerStats$.value);
    }
  }

  submitStats(): void {
    if (this.form.pristine) {
      this._messagingService.info('GENERAL.TOASTS.SAVE_NO_CHANGES.TITLE', 'GENERAL.TOASTS.SAVE_NO_CHANGES.MESSAGE');
      return;
    }
    if (!this._validateForm()) {
      this._messagingService.error('GENERAL.TOASTS.ERROR_SAVE.TITLE', 'GENERAL.TOASTS.ERROR_SAVE.MESSAGE');
      return;
    }

    const statScores: IAnalystStats = this.form.value as IAnalystStats;
    const statRequest: Array<IAnalystDataRequest> = [
      {
        playerId: this.playerDetails$.value?.id || 0,
        analystId: this.analystProfile$.value?.id || 0,
        statScores,
      },
    ];

    this._playerService.savePlayerStats(statRequest);
  }

  private _createForm(analystStats: IAnalystStats): FormGroup {
    const form = this._fb.group({});

    Object.keys(analystStats).forEach((key: string) => {
      // @ts-ignore
      const statValue = analystStats[key];
      form.addControl(key, this._fb.control(statValue, [Validators.required, Validators.min(0), Validators.max(20)]));
    });

    return form;
  }

  private _createStatList(groupName: string): Array<IPlayerAnalystStatList> {
    const auxList: Array<IPlayerAnalystStatList> = [];

    PLAYER_ANALYST_STATS_GROUPED[groupName].forEach(statName => {
      auxList.push({
        statName,
        formControl: this.form.controls[statName] as FormControl,
      });
    });

    return auxList;
  }

  private _validateForm(): boolean {
    return (
      this._checkAllControlsValid(this.physicalStatList) &&
      this._checkAllControlsValid(this.tacticsStatList) &&
      this._checkAllControlsValid(this.mentalStatList)
    );
  }

  private _checkAllControlsValid(formControls: Array<IPlayerAnalystStatList>): boolean {
    return !formControls.find(item => item.formControl.invalid);
  }
}
