import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'teamFlag',
})
export class TeamFlagPipe implements PipeTransform {
  transform(imgUrl: string | undefined): string {
    if (!imgUrl) {
      return 'assets/images/pictures/team_default.png';
    }
    return imgUrl;
  }
}
