import { BehaviorSubject } from 'rxjs';
import { SocialUser } from 'angularx-social-login';

export const fakeSocialUser: SocialUser = {
  email: 'fake@email.com',
  id: '1',
  idToken: 'Provider token',
  provider: 'google',
  lastName: 'FakeLastName',
  firstName: 'FakeFirstName',
  authorizationCode: 'authCode',
  name: 'FakeLastName FakeFirstName',
  authToken: 'authToken',
  photoUrl: 'http://fakeUrl.com/photo.jpeg',
  response: 200,
};

export class SocialAuthServiceMock {
  authState = new BehaviorSubject<SocialUser | undefined>(undefined);

  // Used for interacting with the lib
  clearUser = () => {
    this.authState.next(undefined);
  };

  loadUser = () => {
    this.authState.next(fakeSocialUser);
  };

  // Mock methods
  signOut = () => {
    this.authState.next(undefined);
  };

  signIn = () => {
    this.authState.next(fakeSocialUser);
    return fakeSocialUser;
  };
}
