import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

import { ApiService } from '../system/api.service';
import { API_TYPE_ENUM } from '../../enums';
import { IPayment, IPaymentListItem } from '../../interfaces';
import { MessagingService } from '../ui/messaging.service';

@Injectable()
export class PaymentService {
  paymentSuccessful$ = new BehaviorSubject<boolean | undefined>(undefined);
  isLoading$ = new BehaviorSubject<boolean>(false);
  payments$ = new BehaviorSubject<Array<IPaymentListItem>>([]);

  constructor(private _apiService: ApiService, private _messagingService: MessagingService) {}

  private _handlePaymentSuccessful = (): void => {
    this.paymentSuccessful$.next(true);
    this.isLoading$.next(false);
  };

  private _handlePaymentError = (): void => {
    this.paymentSuccessful$.next(false);
    this.isLoading$.next(false);
  };

  savePayment(payment: IPayment): void {
    this.isLoading$.next(true);
    const url = `payments`;
    this._apiService
      .post(API_TYPE_ENUM.IDENTITY, url, payment)
      .subscribe(this._handlePaymentSuccessful, this._handlePaymentError);
  }

  getPayments(): void {
    this.isLoading$.next(true);
    const url = `payments`;
    this._apiService.get(API_TYPE_ENUM.IDENTITY, url).subscribe(
      (res: any) => {
        this.payments$.next(res?.element || []);
        this.isLoading$.next(false);
      },
      () => {
        this.payments$.next([]);
        this.isLoading$.next(false);
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
      }
    );
  }

  // Resets service to initial state
  resetPayment() {
    this.paymentSuccessful$.next(undefined);
  }
}
