import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { IDefaultResponse, IEntityStatus, ILeague, IPlayerRanking, IStandings } from '../../interfaces';
import { ApiService } from '../system/api.service';
import { MessagingService } from '../ui/messaging.service';
import { API_TYPE_ENUM, STATUS_ENTITIES_ENUM } from '../../enums';
import { SyncStatusService } from './sync-status.service';

@Injectable({
  providedIn: 'root',
})
export class LeaguesService {
  isLoading$ = new BehaviorSubject<boolean>(false);
  selectedLeague$ = new BehaviorSubject<ILeague | undefined>(undefined);
  leagues$ = new BehaviorSubject<Array<ILeague>>([]);
  leagueSyncStatus$ = new BehaviorSubject<IEntityStatus | undefined>(undefined);
  leagueStandings$ = new BehaviorSubject<IStandings | undefined>(undefined);

  ranking$ = new BehaviorSubject<IPlayerRanking[]>([]);
  private _rankingLeague: number | undefined;
  private _rankingRole: number | undefined;

  constructor(
    private _apiService: ApiService,
    private _messagingService: MessagingService,
    private _syncStatusService: SyncStatusService
  ) {
    this.loadLeagues();
    this.isLoading$ = this._syncStatusService.isLoading$;
  }

  loadLeagues = (forceRefresh: boolean = false): void => {
    if ((!this.leagues$.value?.length && !this.isLoading$.value) || forceRefresh) {
      this.isLoading$.next(true);
      const url = `leagues`;
      this._apiService
        .get(API_TYPE_ENUM.LEAGUES, url)
        .pipe(map((res: IDefaultResponse) => res.element))
        .subscribe(
          (res: Array<ILeague>) => {
            this.leagues$.next(res || []);
            this.isLoading$.next(false);
          }
        );
    } else {
      this.leagues$.next(this.leagues$.value);
    }
  };

  getLeaguesSyncStatus = (): void => {
    this._syncStatusService.getEntitySyncStatus(STATUS_ENTITIES_ENUM.LEAGUE).subscribe((result: IEntityStatus) => {
      this.leagueSyncStatus$.next(result);
    });
  };

  getLeagueStandings(seasonId: number): void {
    const url = `seasons/${seasonId}/standings`;

    this._apiService.get(API_TYPE_ENUM.LEAGUES, url).subscribe(
      (res: any) => {
        this.leagueStandings$.next(res.element);
      },
      () => {
        this._messagingService.error('GENERAL.ERRORS.DEFAULT.TITLE', 'GENERAL.ERRORS.DEFAULT.MESSAGE');
      }
    );
  }

  doSyncLeagues(): Observable<boolean> {
    return this._syncStatusService.triggerEntitySyncStatus(STATUS_ENTITIES_ENUM.LEAGUE);
  }

  getLeagueRanking(leagueId: number, roleId: number, records?: number): void {
    if (leagueId !== this._rankingLeague || roleId !== this._rankingRole) {
      this._rankingLeague = leagueId;
      this._rankingRole = roleId;

      let url = `leagues/${leagueId}/players/ranking?roleId=${roleId}`;
      if (records) {
        url += `&records=${records}`;
      }

      this._apiService
        .get(API_TYPE_ENUM.LEAGUES, url)
        .pipe(map((res: IDefaultResponse) => res.element))
        .subscribe(
          (res: IPlayerRanking[]) => {
            this.ranking$.next(res);
          },
          err => {
            console.error('Unable to fetch players ranking', err);
          }
        );
    }
  }

  set selectedLeague(league: ILeague | undefined) {
    this.selectedLeague$.next(league);
  }

  get selectedLeague(): ILeague | undefined {
    return this.selectedLeague$.value;
  }

  findLeagueById(leagueId: number | string): ILeague | undefined {
    return this.leagues$.value.find(l => l.id === +leagueId);
  }
}
