export interface IEvent {
  id: string;
  wyscoutId: number;
  matchId: number;
  matchPeriod: string;
  minute: number;
  second: number;
  matchTimestamp: string;
  videoTimestamp: number;
  type: IEventType;
  teamId: number;
  teamName: string;
  teamScore: string;
  opponentTeamId: number;
  opponentTeamName: string;
  opponentTeamScore: string;
  playerId: number;
  playerName: string;
  leagueId: number;
  seasonId: number;
  typeShortLabel: string;
  typeLabel: string;
  label: string;
}

export interface IEventType {
  primary: string;
  secondary: string[];
}

type Score ={
  score:number;
  data:IEvent[]
}
export interface IEventScore{
  type:string,
  home:Score,
  away:Score
}
export interface IEventPeriodScore{
  period:string,
  label:string,
  home:IEvent[],
  away:IEvent[]
}
