import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import {
  FixtureService,
  IMatch,
  IPlayer,
  IPlayerStats,
  IPlayerStatsExtended,
  ISeason,
  PLAYER_ROLES_STATS_ARRAY,
  PlayerService,
  LeaguesService,
  ILeague,
} from '@aft-core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { FormBuilder, FormControl } from '@angular/forms';

@Component({
  selector: 'aft-wyscout-stats',
  templateUrl: './player-wyscout-stats.component.html',
  styleUrls: ['./player-wyscout-stats.component.scss'],
})
export class PlayerWyscoutStatsComponent implements OnInit, OnDestroy {
  @Input()
  showPlayerResume: boolean = true;

  playerDetails$: BehaviorSubject<IPlayer | undefined>;
  playerStats$: BehaviorSubject<IPlayerStats | undefined>;
  playerStatsExtended$: BehaviorSubject<Array<IPlayerStatsExtended> | undefined>;
  matches$: BehaviorSubject<Array<IMatch>>;
  leagues$: BehaviorSubject<ILeague[]>;

  buttonOptions: Array<string> = [];
  selectedCategory: FormControl;

  activeSeason: ISeason | undefined;

  tableValues: Array<any> = [];
  tableColumns: Array<string> = [];

  private _playerRolesStats = PLAYER_ROLES_STATS_ARRAY;
  private _subscriptions: Subscription[] = [];

  constructor(private _leaguesService: LeaguesService,private _playerService: PlayerService, private _fb: FormBuilder, private _fixtureService: FixtureService) {
    this.leagues$ = this._leaguesService.leagues$;
    this.playerDetails$ = this._playerService.playerDetails$;
    this.playerStats$ = this._playerService.playerStats$;
    this.playerStatsExtended$ = this._playerService.playerStatsExtended$;
    this.matches$ = this._fixtureService.matches$;

    this.selectedCategory = this._fb.control({});
  }

  ngOnInit(): void {
    // Takes care of the variables seen on screen
    // Triggered when player Details finishes loading for the first time
    this._subscriptions.push(
      this.selectedCategory.valueChanges.subscribe(category => {
        if (category) {
          this._buildTableData();
        }
      })
    );

    // When the extended stats are loaded, load the match list
    this._subscriptions.push(
      this.playerStatsExtended$.subscribe(playerStats => {
        if (playerStats) {
          this._fixtureService.loadMatchesInfo(playerStats.map(pStat => pStat.matchId));
        }
      })
    );

    this._subscriptions.push(
      this.playerDetails$.subscribe(player => {
        if (player) {
          // Initializes buttons depending on role id of the player
          this._initializeButtonOptions(player);

          if(player.currentTeam?.league === null){
            this.leagues$.subscribe(res=>{
              if(player.currentTeam){
                player.currentTeam.league = res.find( league => league.id === player.currentTeam?.leagueId);
              }
            })
          }
          
          // Fetches Player extended stats info
          this.activeSeason = player.currentTeam?.league?.seasons?.find(s => s.active);
          if (this.activeSeason) {
            this._playerService.getPlayerExtendedStats(player.id, this.activeSeason.id);
          }
        }
      })
    );

    // Takes care of joining the data and building the table
    this._subscriptions.push(
      this.matches$.subscribe(matches => {
        if (matches) {
          this._buildTableData();
        }
      })
    );
  }

  calculateStatTotal(index: number): number {
    let total = 0;
    this.tableValues.forEach(matchStat => {
      total += matchStat[index];
    });
    return +total.toFixed(2);
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  private _initializeButtonOptions(player: IPlayer): void {
    this.buttonOptions = [];
    // @ts-ignore
    const statColumns = this._playerRolesStats[player.roleId];

    Object.keys(statColumns).forEach(col => {
      this.buttonOptions.push(col);
    });

    this.selectedCategory.setValue(this.buttonOptions[0]);
  }

  private _buildTableData(): void {
    const matchesList = this.matches$.value as Array<IMatch>;
    const positionCategory = this.selectedCategory.value;

    if (matchesList.length && positionCategory.length) {
      this.tableColumns = [];
      this.tableValues = [];

      const playerDetails = this.playerDetails$.value;
      const playerStats = this.playerStatsExtended$.value as Array<IPlayerStatsExtended>;

      // @ts-ignore
      const statColumns = this._playerRolesStats[playerDetails.roleId];
      const categoryStats = statColumns[positionCategory];

      // Hardcoded column name
      this.tableColumns.push('matches');

      matchesList.forEach(match => {
        const matchStat = playerStats.find(s => s.matchId === match.id);
        if (matchStat) {
          const aux = [match];

          categoryStats.forEach((categoryStat: string) => {
            if (!this.tableColumns.includes(categoryStat)) {
              this.tableColumns.push(categoryStat);
            }

            // @ts-ignore
            aux.push(matchStat.total[categoryStat]);
          });

          this.tableValues.push(aux);
        }
      });
    }
  }
}
