import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { BehaviorSubject, Subscription } from 'rxjs';
import { AppConfigService, IPlayer, PLATFORM_ENUM, TeamsPlayersService } from '@aft-core';
import { PlayerAgePipe } from '@sw-ui-components';
import { Router } from '@angular/router';
import { generateFullPrivateModuleRoute as generateScoutingRoutes } from '@scouting/private/private.routes';
import { PRIVATE_MODULE_ROUTES as SCOUTING_ROUTES } from '@scouting/private/private.routes';
import { generatePlayerModuleRoute, PLAYER_MODULE_ROUTES } from '@backoffice/private/player/player.routes';

@Component({
  selector: 'aft-player-list',
  templateUrl: './player-list.component.html',
  styleUrls: ['./player-list.component.scss'],
})
export class PlayerListComponent implements OnInit, OnDestroy {
  @Input()
  hideTeams: boolean = false;

  loading$: BehaviorSubject<boolean>;
  filteredPlayers: Array<IPlayer>;

  form: FormGroup;

  showAlert = true;

  private _players: Array<IPlayer>;
  private _subscriptions: Subscription[] = [];

  constructor(
    private _fb: FormBuilder,
    private _playersService: TeamsPlayersService,
    private _playerAgePipe: PlayerAgePipe,
    private _appConfigService: AppConfigService,
    private _router: Router
  ) {
    this.loading$ = this._playersService.isLoading$;
    this._players = this._playersService.players$.value;
    this.filteredPlayers = this._playersService.players$.value;

    this.form = this._fb.group({
      searchString: [''],
    });
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this._playersService.players$.subscribe(players => {
        this._players = players;
        this.filteredPlayers = players;
      })
    );

    this._subscriptions.push(
      this.form.controls.searchString.valueChanges.subscribe(newValue => {
        // Filters list based on the search string
        this.filteredPlayers = this._players.filter((player: IPlayer) => {
          const lowerCaseStringVal = newValue.toLowerCase();
          return (
            player.firstName.toLowerCase().includes(lowerCaseStringVal) ||
            player.lastName?.toLowerCase().includes(lowerCaseStringVal) ||
            player.birthArea?.name.toLowerCase().includes(lowerCaseStringVal) ||
            player.role?.code3?.toLowerCase().includes(lowerCaseStringVal) ||
            player.currentTeam?.name.toLowerCase().includes(lowerCaseStringVal) ||
            this._playerAgePipe.transform(player.birthDate).includes(lowerCaseStringVal)
          );
        });
      })
    );
  }

  goToPlayerDetails(player: IPlayer): void {
    const isBackoffice = this._appConfigService.appConfig.platform === PLATFORM_ENUM.BACKOFFICE;
    if (isBackoffice) {
      this._router.navigate(generatePlayerModuleRoute(PLAYER_MODULE_ROUTES.PLAYER_INFO_ROUTE, player.id?.toString()));
    } else {
      this._router.navigate(generateScoutingRoutes(SCOUTING_ROUTES.PLAYER, player.playerId));
    }
  }

  // Destroys subscriptions and clears service
  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  // Hides sync alert
  hideAlert(): void {
    this.showAlert = false;
  }
}
